import React, { useState } from "react";


import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';



const GetPassItemForGuard = (props) => {

    const [modalOpen, setModalOpen] = useState(false);
    const [teacher, setTeacher ] = useState("")

    console.log(props)

    const handleShow = async () => {
        setModalOpen(true);

        const teacherResponse = await fetch(process.env.REACT_APP_BACKEND_URL + "/teacher/auth/getteacher/" + props.teacher_id);
        const teacherResponse_data = await teacherResponse.json();
        setTeacher(teacherResponse_data.first_name + " "+ teacherResponse_data.last_name);
        console.log(teacherResponse_data.first_name)
            


    }

    const handleClose = () => {
        setModalOpen(false);

    }

    return (
        <>
            <li onClick={handleShow} className="list-group-item p-3 d-flex flex-row justify-content-between">
                <h5>{props.student_name}</h5>
                <h5>{props.student_year}</h5>
            </li>

            <Modal show={modalOpen} onHide={handleClose} centered>
                <Modal.Header closeButton>
                    <Modal.Title className="text-center">{props.student_name}  ( {props.student_year} )</Modal.Title>
                </Modal.Header>
                <Modal.Body>

                    <div className="card">
                        <div className="card-body ">
                            <h4 className="card-title pt-3">Name : {props.student_name}</h4>
                            <h4 className="card-title pt-3">Class : {props.student_year}</h4>


                            {/* <h5 className="card-text pt-3">Reason : {props.reason}</h5> */}

                            <div className="d-flex justify-content-between">

                                <h4 className="card-title pt-3">In Time : {props.in_time}</h4>
                                <h4 className="card-title px-5 pt-3">In Time : {props.out_time}</h4>
                            </div>

                            <h4 className="card-title pt-3">Teacher : {teacher}</h4>
                        </div>
                    </div>



                </Modal.Body>
                <Modal.Footer className="d-flex flex-row justify-content-center gap-5">
                    <Button variant="success" className="px-5" onClick={handleClose}>
                        Close
                    </Button>
                </Modal.Footer>
            </Modal>
        </>


    )
}

export default GetPassItemForGuard
