import React, { useEffect, useState } from "react";

import { useSelector } from "react-redux";
import GetPassItemForTeacher from "./GetPassItemForTeacher";
import { socket } from "../App";
import notificationAudio from "../assert/notification.mp3"
import { toast } from "react-toastify";


const TeacherGetPassRequestsList = () => {


  const [ getPassRequestsList, setGetPassRequestsList] = useState([]);

  const {userid} = useSelector((state)=>state.authReducer);

  useEffect(()=>{
    
    socket.on("getpassrequest", (data)=>{
      
      setGetPassRequestsList(oldArray=>[data, ...oldArray]);
      try{
        const notific = new Audio(notificationAudio);
        toast.success(`${data.student_name} ${data.student_year} Send Request`, {
          position: "top-center",
          autoClose: 2000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "colored",
      });
        notific.play().then((success)=>{}).catch((error)=>{});
      }
      catch(error){

      }
    });


    const getAllGetPasss = async ()=>{
      const response = await fetch(process.env.REACT_APP_BACKEND_URL+"/getpass/getpassrequestsbyteacher?id="+userid);

      const getpasss = await response.json();

      console.log(getpasss);

      if(response.status === 200){
        setGetPassRequestsList(getpasss);
      }
    };

    getAllGetPasss();

    return () => {
      socket.removeListener('getpassrequest');
      socket.off('getpassrequest');
      socket.removeAllListeners('getpassrequest');
    };


  }, [userid]);


  const removeGetPassInListById = (id) =>{
    const newGetPassRequestsList = getPassRequestsList.filter((getpass)=>{
      return getpass._id !== id;
    });

    setGetPassRequestsList(newGetPassRequestsList);

  }




  return (
    <div className="my-3">
      <h3 className="my-4 text-center">GetPass Requests</h3>
      <ul className="list-group">
        <li className="list-group-item p-3 d-flex flex-row justify-content-between">
          <h5>Name</h5>
          <h5>Class</h5>
        </li>


        {getPassRequestsList.map((getpasss, index)=>{
          return <GetPassItemForTeacher removeGetPassInListById={removeGetPassInListById} key={index} {...getpasss} />
        })}

      </ul>

      
    </div>


  )
}

export default TeacherGetPassRequestsList
