import React, { useRef } from 'react'
import { useDispatch } from 'react-redux';
import { Link } from 'react-router-dom'
import { signupGuardAction } from '../store/actions';
import NavBar from './NavBar'

const GuardSignUp = () => {

    const first_name_ref = useRef();
    const last_name_ref = useRef();
    const email_ref = useRef();
    const password_ref = useRef();


    const dispatch = useDispatch();




    const handalSignup = (event) => {
        event.preventDefault();

        dispatch(signupGuardAction({
            first_name: first_name_ref.current.value,
            last_name: last_name_ref.current.value,
            email: email_ref.current.value,
            password: password_ref.current.value,
        }));
    }







    return (
        <>
            <NavBar />
            <div className=' mt-5 container d-flex flex-column align-items-center'>
                <form onSubmit={handalSignup} style={{ width: "380px" }}>
                    <h2 className='text-center p-3'>Guard Sign Up</h2>


                    <div className="form-group m-3">
                        <input required ref={first_name_ref} type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="First Name" />

                    </div>

                    <div className="form-group m-3">
                        <input required ref={last_name_ref}  type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Last Name" />

                    </div>

                    <div className="form-group m-3">
                        <input required ref={email_ref} type="email" className="form-control" id="exampleInputPassword1" placeholder="Email" />
                    </div>

                    <div className="form-group m-3">
                        <input required ref={password_ref} type="password" className="form-control" id="exampleInputPassword1" placeholder="Password" />
                    </div>

                    <div>
                        <Link className='m-3' to={"/guard/login"}>Already have an account Log in</Link>
                    </div>
                    <div className='text-center m-4'>

                        <button style={{ width: "100px" }} type="submit" className="btn btn-primary">Sign Up</button>
                    </div>
                </form>
            </div>
        </>
    )
}


export default GuardSignUp;
