import { toast } from "react-toastify"

export const loginStudentAction = (data) => {
    return async (dispatch) => {

        try {

            const response = await fetch(process.env.REACT_APP_BACKEND_URL + "/student/auth/login", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({
                    email: data.payload.email,
                    password: data.payload.password,
                })
            })

            const response_data = await response.json()

            console.log(response.status)

            if (response.status === 200) {
                dispatch({
                    type: "studentloginsuccess",
                    payload: {
                        userid: response_data._id,
                        username: response_data.first_name + " " + response_data.last_name,
                        token: response_data._id,
                        dept: response_data.dept,
                        email: response_data.email,
                        year: response_data.year,
                    }
                })
                toast.success('Loged In', {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "colored",
                });
            }
            else {

                toast.error(response_data.detail, {
                    position: "top-center",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "colored",
                    });
            }

        }
        catch (error) {
            toast.error("Error", {
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "colored",
                });
        }



    }
}






