import React, { useEffect, useRef, useState } from 'react'
import NavBar from '../componets/NavBar';

import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import StudentGetPasssList from '../componets/StudentGetPasssList';
import { useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { io } from "socket.io-client";
import { toast } from 'react-toastify';

import notificationAudio from "../assert/notification.mp3"

const socket = io(process.env.REACT_APP_BACKEND_URL);


const StudentPage = () => {



  const reason_ref = useRef();
  const teacher_ref = useRef();
  const intime_ref = useRef();
  const outtime_ref = useRef();


  const navigate = useNavigate();

  const [reciptMsg, setReciptMsg] = useState();

  const [teachersList, setTeachersList] = useState([]);


  const { usertype, userid, username, dept, year } = useSelector((state) => state.authReducer)

  const [getpassAccedptedData, setGetpassAccedptedData] = useState({});

  useEffect(() => {
    if (usertype === "teacher") {
      navigate("/teacher");
    }
    else if (usertype === "guard") {
      navigate("/guard");
    }

    socket.emit("join", userid);
    socket.on("accepted", (data) => {
      console.log("Accepted", data);


      try {
        const notific = new Audio(notificationAudio);
        toast.success(`Your Request Accepted ${data.teacher_id}`, {
          position: "top-center",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "colored",
        });
        notific.play().then((success) => { }).catch((error) => { });
      }
      catch (error) {

      }

    });




    const getTeacherOfDept = async () => {

      try {

        const response = await fetch(process.env.REACT_APP_BACKEND_URL + "/getpass/getteacherbydept?dept=" + dept);

        const techers = await response.json();
        setTeachersList(techers);
      }
      catch (error) {
        alert("Error");
      }

    }
    getTeacherOfDept();

    return () => {
      socket.removeListener('accepted');
      socket.off('accepted');
      socket.removeAllListeners('accepted');
    };

  // eslint-disable-next-line
  }, [socket]);



  const [modalOpen, setModalOpen] = useState(false);
  const [errorModalOpen, errorSetModalOpen] = useState(false);

  const handleShow = async () => {

    const response = await fetch(process.env.REACT_APP_BACKEND_URL + "/getpass/getrecept?id=" + userid);
    
    const response_data = await response.json();
    
    if (response.status === 200) {
      setReciptMsg("Accepted");
      setModalOpen(true);
      
      const teacherResponse = await fetch(process.env.REACT_APP_BACKEND_URL + "/teacher/auth/getteacher/" + response_data.teacher_id);
      const teacherResponse_data = await teacherResponse.json();
      response_data.teachername = teacherResponse_data.first_name + " "+ teacherResponse_data.last_name;

      setGetpassAccedptedData(response_data);
    }
    else {
      setReciptMsg(response_data.detail);
      // errorSetModalOpen(true);

      toast.error(response_data.detail, {
        position: "top-center",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "colored",
      });
    }



  }

  const handleClose = () => {
    setModalOpen(false);
  }

  const handleErrorClose = () => {
    errorSetModalOpen(false);
  }


  const hadalApply = async (event) => {
    event.preventDefault();

    const reason = reason_ref.current.value;
    const teacherId = teacher_ref.current.value;
    const intime = intime_ref.current.value;
    const outtime = outtime_ref.current.value;

    reason_ref.current.value = "";
    intime_ref.current.value = "";
    outtime_ref.current.value = "";


    try {

      console.log(year)

      const response = await fetch(process.env.REACT_APP_BACKEND_URL + "/getpass/request", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          student_id: userid,
          student_name: username,
          student_year: year,
          teacher_id: teacherId,
          reason: reason,
          in_time: intime,
          out_time: outtime,
        })
      });

      const response_data = await response.json();

      if (response.status === 200) {
        socket.emit("applybystudent", response_data);
        toast.success("Send to Your Teacher", {
          position: "top-center",
          autoClose: 2000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "colored",
        });


      }
      else {
        toast.error(response_data.detail, {
          position: "top-center",
          autoClose: 2000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "colored",
        });
      }
    }
    catch (error) {
      toast.error("Error", {
        position: "top-center",
        autoClose: 2000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "colored",
      });
    }

  }




  return (
    <div>
      <NavBar />
      <div className="container">

        <div className="card mt-4">
          <div className="card-body">
            <h3>{username}</h3>
          </div>
        </div>

        <div>
          <button className="btn btn-primary mt-3" type="submit">Request Getpass</button>
          <Button className='mx-4 mt-3' variant="success" onClick={handleShow}>
            Get Pass Recipt
          </Button>

        </div>

        <form onSubmit={hadalApply}>

          <label htmlFor="exampleFormControlTextarea1" className="form-label mt-3">Reason</label>
          <textarea ref={reason_ref} required className="form-control" id="exampleFormControlTextarea1" rows="2"></textarea>

          <label htmlFor="teacherslist" className="form-label mt-3">Select Teacher</label>
          <select ref={teacher_ref} required className="form-select" aria-label="Default select example" id='teacherslist'>
            {teachersList.map((techs) => {
              return <option key={techs._id} value={techs._id}>{techs.first_name + " " + techs.last_name}</option>
            })}

          </select>

          <div className='d-flex gap-5 my-3'>

            <div className=''>

              <label htmlFor="intime">In Time</label>
              <input ref={intime_ref} required type="time" style={{ display: "block" }} id="intime" />
            </div>

            <div>
              <label htmlFor="outtime">Out Time</label>
              <input ref={outtime_ref} required type="time" style={{ display: "block" }} id="outtime" />
            </div>
          </div>
          <button className="btn btn-success my-2" type="submit">Apply</button>
        </form>

        <StudentGetPasssList />
      </div>


      <Modal show={modalOpen} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Accepted by : {getpassAccedptedData.teachername} (sir)</Modal.Title>
        </Modal.Header>
        <Modal.Body>

          <div className="card">
            <div className="card-body ">
              <h4 className="card-title pt-3">Name : {getpassAccedptedData.student_name}</h4>
              <h4 className="card-title pt-3">Class : {getpassAccedptedData.student_year}</h4>


              <h5 className="card-text pt-3">Reason : {getpassAccedptedData.reason}</h5>

              <div className="d-flex justify-content-between">

                <h4 className="card-title pt-3">In Time : {getpassAccedptedData.in_time}</h4>
                <h4 className="card-title px-5 pt-3">In Time : {getpassAccedptedData.out_time}</h4>
              </div>

                <h4 className="card-title pt-3">Teacher : {getpassAccedptedData.teachername}</h4>

            </div>
          </div>

        </Modal.Body>
        <Modal.Footer>
          <Button variant="success" onClick={handleClose}>
            Close
          </Button>

        </Modal.Footer>
      </Modal>


      <Modal show={errorModalOpen} onHide={handleErrorClose}>
        <Modal.Header closeButton>
          <Modal.Title>Name</Modal.Title>
        </Modal.Header>
        <Modal.Body>{reciptMsg}</Modal.Body>
        <Modal.Footer>
          <Button variant="danger" onClick={handleErrorClose}>
            Close
          </Button>

        </Modal.Footer>
      </Modal>








    </div>
  )
}

export default StudentPage
