import React, { useEffect, useState }  from "react";
import GetPassItemForGuard from "./GetPassItemForGuard";

import { toast } from "react-toastify";

import notificationAudio from "../assert/notification.mp3"

import { socket } from "../App";


const GuardGetPassAcceptedList = () => {

    const [acceptedGetPass, setAcceptedGetPass] = useState([]);

    console.log("Guard List Page");

    useEffect(()=>{
        socket.on("acceptedtoguard", (data) => {
            setAcceptedGetPass((oldGetPass)=>[data,...oldGetPass]);
            console.log("Accepted", acceptedGetPass);


            try {
                const notific = new Audio(notificationAudio);
                notific.play().then((success) => { }).catch((error) => { });
                toast.success(`New Get Pass`, {
                  position: "top-center",
                  autoClose: 5000,
                  hideProgressBar: false,
                  closeOnClick: true,
                  pauseOnHover: true,
                  draggable: true,
                  progress: undefined,
                  theme: "colored",
                });
              }
              catch (error) {
        
              }
        });

        const getAcceptedGetpass = async()=>{

            const response = await fetch(process.env.REACT_APP_BACKEND_URL + "/getpass/acceptedgetpass");

            const response_data = await response.json();
            setAcceptedGetPass(response_data);

        }

        getAcceptedGetpass();

        return () => {
          socket.off('acceptedtoguard', );
          
        };

    },[])


  return (
    <div className="my-3">
    <h3 className="my-4 text-center">Accepted GetPass</h3>
    <ul className="list-group">
      <li className="list-group-item p-3 d-flex flex-row justify-content-between">
        <h5>Name</h5>
        <h5>Class</h5>
      </li>


      {acceptedGetPass.map((getpasss, index)=>{
        return <GetPassItemForGuard key={index} {...getpasss} />
      })}

    </ul>

    
  </div>
  )
}

export default GuardGetPassAcceptedList;
