import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import NavBar from './NavBar'


import { loginStudentAction } from '../store/actions';
import { useDispatch } from 'react-redux';


const StudentLogin = () => {

  const dispatch = useDispatch();

  const [ loginState, setLoginState ] = useState({email : "", password : ""});


  
  const onChangeHandaler = (event)=>{
    setLoginState({...loginState,[event.target.name]: event.target.value});
  }

  const loginHandaler = (event)=>{
    event.preventDefault();
    console.log(loginState);

    dispatch(loginStudentAction({type : "studentlogin" , payload : loginState}))
    

  }



  return (
    <>
    <NavBar />
    <div className=' mt-5 container d-flex flex-column align-items-center'>
      <form onSubmit={loginHandaler} style={{width:"380px"}}>
        <h2 className='text-center p-3'>Student Login</h2>
  <div className="form-group m-3">
    <input onChange={onChangeHandaler} name='email' type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter email" />
    
  </div>
  <div className="form-group m-3">
    <input onChange={onChangeHandaler} name='password' type="password" className="form-control" id="exampleInputPassword1" placeholder="Password" />
  </div>
  <div>
    <Link className='m-3' to={"/student/signup"}>Don't have an Account SignUp</Link>
  </div>
    <div className='text-center m-4'>

  <button style={{width:"100px"}} type="submit" className="btn btn-primary">Login</button>
    </div>
</form>
    </div>
    </>
  )
}


export default StudentLogin;
