import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";

const StudentGetPasssList = () => {

    const { userid } = useSelector((state) => state.authReducer)

    const [getPassList, setGetPassList] = useState([]);


    useEffect(() => {
        const getPrevGetpass = async () => {
            const response = await fetch(process.env.REACT_APP_BACKEND_URL + "/getpass/studentgetpasslist?id=" + userid);
            const response_data = await response.json();
            setGetPassList(response_data);
        }
        getPrevGetpass();
    }, [userid])


    return (
        <div className="my-3">
            <h3 className="my-2">previous GetPass</h3>
            <ul className="list-group">

                {getPassList.map((getpass) => {
                    return (
                        <li className="list-group-item d-flex justify-content-between align-items-center">
                            {getpass.reason}
                            {
                                getpass.accepted ?
                                <button className="btn btn-success px-4">View</button> :
                                <button className="btn btn-danger">Pending</button>
                            }

                        </li>
                    )
                })}



            </ul>
        </div>
    )
}

export default StudentGetPasssList;
