import React, { useState } from "react";


import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { socket } from "../App";



const GetPassItemForTeacher = (props) => {

    const [modalOpen, setModalOpen] = useState(false);


    const handleShow = () => {
        setModalOpen(true);
    }

    const handleClose = () => {
        setModalOpen(false);

    }
    const handalAcceptGetpass = async () => {

        try {

            const response = await fetch(process.env.REACT_APP_BACKEND_URL + "/getpass/accept?id=" + props._id);

            const response_data = await response.json();
            props.removeGetPassInListById(props._id);
            socket.emit("acceptedbyteacher", response_data);


        }
        catch (error) {
            alert("Error");
        }



        setModalOpen(false);
    }


    return (
        <>
            <li onClick={handleShow} className="list-group-item p-3 d-flex flex-row justify-content-between">
                <h5>{props.student_name}</h5>
                <h5>{props.student_year}</h5>
            </li>

            <Modal show={modalOpen} onHide={handleClose} centered>
                <Modal.Header closeButton>
                    <Modal.Title className="text-center">{props.student_name} ( {props.student_year} )</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div>
                        <h4 className="text-center my-2 mb-4 pb-3">
                            {props.reason}
                        </h4>
                    </div>
                    <hr className="mt-5 pt-4 mb-0" />

                    <div className="d-flex justify-content-around">
                        <div className="d-flex flex-column">
                            <h6 className="text-center">In Time</h6>
                            <h6 className="text-center">{props.in_time}</h6>
                        </div>

                        <div className="d-flex flex-column">
                            <h6 className="text-center">Out Time</h6>
                            <h6 className="text-center">{props.out_time}</h6>
                        </div>
                    </div>
                </Modal.Body>
                <Modal.Footer className="d-flex flex-row justify-content-center gap-5">
                    <Button onClick={handalAcceptGetpass} variant="success">
                        Accept
                    </Button>
                    <Button variant="danger" onClick={handleClose}>
                        Reject
                    </Button>
                </Modal.Footer>
            </Modal>


        </>


    )
}

export default GetPassItemForTeacher
