import React, { useRef } from 'react'
import { useDispatch } from 'react-redux';
import { Link } from 'react-router-dom'
import { signupStudentAction } from '../store/actions';
import NavBar from './NavBar'
const StudentSignup = () => {



    const first_name_ref = useRef();
    const last_name_ref = useRef();
    const email_ref = useRef();
    const password_ref = useRef();
    const dept_ref = useRef();
    const year_ref = useRef();

    const dispatch = useDispatch();




    const handalSignup = (event) => {
        event.preventDefault();

        dispatch(signupStudentAction({
            first_name: first_name_ref.current.value,
            last_name: last_name_ref.current.value,
            email: email_ref.current.value,
            password: password_ref.current.value,
            dept: dept_ref.current.value,
            year: year_ref.current.value
        }));






    }



    return (
        <>
            <NavBar />
            <div className=' mt-3 container d-flex flex-column align-items-center'>
                <form onSubmit={handalSignup} style={{ width: "380px" }}>
                    <h2 className='text-center p-1'>Student Sign Up</h2>


                    <div className="form-group m-3">
                        <input required ref={first_name_ref} name="first_name" type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="First Name" />

                    </div>

                    <div className="form-group m-3">
                        <input required ref={last_name_ref} name='last_name' type="text" className="form-control" id="exampleInputEmail2" aria-describedby="emailHelp" placeholder="Last Name" />

                    </div>

                    <div className="form-group m-3">
                        <select ref={year_ref} name='year' className="form-control">
                            <option defaultValue value="FY">FY</option>
                            <option value="SY">SY</option>
                            <option value="TY">TY</option>
                            <option value="B.Tech">B.Tech</option>
                        </select>
                    </div>

                    <div className="form-group m-3">
                        <select ref={dept_ref} name='dept' className="form-control">
                            <option defaultValue value="CSE">CSE</option>
                            <option value="MECH">MECH</option>
                            <option value="Civil">Civil</option>
                        </select>
                    </div>



                    <div className="form-group m-3">
                        <input required ref={email_ref} name="email" type="email" className="form-control" id="exampleInputPassword1" placeholder="Email" />
                    </div>

                    <div className="form-group m-3">
                        <input required ref={password_ref} name="password" type="password" className="form-control" id="exampleInputPassword" placeholder="Password" />
                    </div>

                    <div>
                        <Link className='m-3' to={"/student/login"}>Already have an account Log in</Link>
                    </div>
                    <div className='text-center m-4'>

                        <button style={{ width: "100px" }} type="submit" className="btn btn-primary">Sign Up</button>
                    </div>
                </form>
            </div>
        </>
    )
}


export default StudentSignup;

