
export const authReducer = (state = {usertype : null, userid : null, username : null,email : null, logedin : false, token: null}, action)=>{

    if(action.type === "studentloginsuccess"){
        return {...state, logedin: true, userid : action.payload.userid, username: action.payload.username, usertype : "student", token: action.payload.token, dept : action.payload.dept, email : action.payload.email, year: action.payload.year}
    }


    else if (action.type === "studentsignupsuccess"){
        return {...state, logedin: true, userid : action.payload.userid, username: action.payload.username, usertype : "student", token: action.payload.token, dept : action.payload.dept, email : action.payload.email,year: action.payload.year}
    }


    else if (action.type === "teacherloginsuccess"){
        return {...state, logedin: true, userid : action.payload.userid, username: action.payload.username, usertype : "teacher", token: action.payload.token , email : action.payload.email}
    }

    else if (action.type === "teachersignupsuccess"){
        return {...state, logedin: true, userid : action.payload.userid, username: action.payload.username, usertype : "teacher", token: action.payload.token , email : action.payload.email}
    }

    else if (action.type === "guardloginsuccess"){
        return {...state, logedin: true, userid : action.payload.userid, username: action.payload.username, usertype : "guard", token: action.payload.token , email : action.payload.email}
    }

    else if (action.type === "guardsignupsuccess"){
        return {...state, logedin: true, userid : action.payload.userid, username: action.payload.username, usertype : "guard", token: action.payload.token , email : action.payload.email}
    }


    
    else if(action.type === "logout"){
        localStorage.clear();
        return {usertype : null, userid : null, username : null,email : null, logedin : false, token: null}
    }
    else{
        return state
    }
}
