import './App.css';
import { Route, Routes } from 'react-router-dom';
import React, { useEffect } from 'react'
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';


import HomePage from './pages/HomePage';

import StudentPage from './pages/StudentPage';
import TeacherPage from './pages/TeacherPage';
import TeacherLogin from './componets/TeacherLogin';
import TeacherSignUp from './componets/TeacherSignUp';
import StudentLogin from './componets/StudentLogin';
import StudentSignup from './componets/StudentSignup';


import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import GuardPage from './pages/GuardPage';
import GuardSignUp from './componets/GuardSignUp';
import GuardLogin from './componets/GuardLogin';

import { io } from "socket.io-client"

const socket = io(process.env.REACT_APP_BACKEND_URL);



const  App = React.memo(()=> {

  const navigate = useNavigate();

  const { userid, usertype,logedin } = useSelector((state)=>state.authReducer)

  useEffect(()=>{
    console.log("App.js");
    
    if (!logedin){
      navigate("/")
    }
    else{

      if(usertype === "student"){
        navigate("/student");
        socket.emit("join", userid);
      }
      else if(usertype === "teacher"){
        socket.emit("join", userid);
        navigate("/teacher");
        
      }
      else if(usertype === "guard"){
        navigate("/guard");
        socket.emit("join", "guardid");
        console.log(usertype)
      }

    }
    
  }, [logedin,userid])

  return (
    <>
      <ToastContainer />

      <Routes>
        <Route path='/' element=<HomePage/> />
        <Route path='/student/login' element=<StudentLogin/> />
        <Route path='/student/signup' element=<StudentSignup/> />
        <Route path='/teacher/login' element=<TeacherLogin/> />
        <Route path='/teacher/signup' element=<TeacherSignUp/> />
        <Route path='/guard/login' element=<GuardLogin/> />
        <Route path='/guard/signup' element=<GuardSignUp/> />
        <Route path='/student' element=<StudentPage/> />
        <Route path='/teacher' element=<TeacherPage/> />
        <Route path='/guard' element=<GuardPage/> />
      </Routes>
    </>
  );
});

export {socket};
export default App;
